<template>
  <h2 class="bg-warning p-3 m-0 text-center">Baby Trello</h2>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-4 col-sm-12 bg-dark p-0">
        <DropZone>
          <Card v-for="(card, index) in cards" :key="index" :id="'card-' + index">{{ card }}</Card>
        </DropZone>
      </div>
      <div v-for="n in dropZones" :key="n" :class="n % 2 === 0 ? 'bg-dark' : 'bg-secondary'"
           class="col-md-4 col-sm-12 p-0">
        <DropZone/>
      </div>
    </div>
  </div>
  <div class="bg-warning p-3 position-fixed w-50 bottom-0 text-center">
    <button class="btn btn-dark" @click="pushCard()">Add card</button>
  </div>
  <div class="bg-danger p-3 position-fixed w-50 bottom-0 right-0 text-center">
    <button class="btn btn-dark" @click="()=>{dropZones++}">Add dropzone</button>
  </div>
</template>

<script>
import DropZone from "@/components/ToDoList/DropZone"
import Card from "@/components/ToDoList/Card"

export default {
  components: {DropZone, Card},
  data() {
    return {
      dropZones: 0,
      cards: []
    }
  },
  methods: {
    pushCard() {
      this.cards.push('card ' + (this.cards.length + 1).toString());
    },
  },
}
</script>

<style scoped>

.right-0 {
  right: 0;
}

</style>
