<template>
  <div :id="id"
       class="card mb-3"
       draggable="true" @dragstart="onDragStart" @dragend="onDragEnd" @dragover.stop>
    <div class="card-body text-center fw-bolder text-uppercase p-3">
      <slot/>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    id: String
  },
  data() {
    return {}
  },
  methods: {
    onDragStart(e) {
      const target = e.target
      e.dataTransfer.setData('card_id', e.target.id)
      setTimeout(() => {
        target.style.display = 'none'
      }, 0)
    },
    onDragEnd(e) {
      e.target.style.display = 'block'
    },
  }
}
</script>

