<template>
<div :id="id" @dragover.prevent @drop.prevent="drop" class="p-3 dropzone">
  <slot/>
</div>
</template>

<script>
export default {
  props: {
    id: String
  },
  methods: {
    drop: function (e) {
      const card = document.getElementById(e.dataTransfer.getData('card_id'))
      e.target.appendChild(card)
    }
  }
}
</script>

<style scoped lang="scss">
.dropzone {
  min-height: 0;
}
</style>
